@charset "UTF-8";
@font-face {
  font-family: "Cabin";
  font-style: normal;
  font-weight: 400;
  src: url("/assets/webfonts/cabin-latin-regular.eot?v=14");
  src: local("Cabin"), local("Cabin-Regular"), url("/assets/webfonts/cabin-latin-regular.eot?v=14#iefix") format("embedded-opentype"), url("/assets/webfonts/cabin-latin-regular.woff2?v=14") format("woff2"), url("/assets/webfonts/cabin-latin-regular.woff?v=14") format("woff"), url("/assets/webfonts/cabin-latin-regular.ttf?v=14") format("truetype"), url("/assets/webfonts/cabin-latin-regular.svg?v=14#Cabin") format("svg");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote::before, blockquote::after,
q::before, q::after {
  content: "Â ";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* For all devices */
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font: 400 14px "Cabin", sans-serif;
  font-family: "Cabin", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  color: #333333;
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 15px;
}

/* Only for Desktops */
@media only screen and (min-width: 768px) {
  body {
    font-size: 15px;
    line-height: 20px;
  }
}
/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  body {
    font-size: 16px;
    line-height: 21px;
  }
}
.error {
  font-size: 16px;
  line-height: 1.2;
  color: #f74440;
}
.error a {
  font-weight: 700;
  color: inherit;
  text-decoration: none;
  border-bottom: transparent solid 2px;
  transition: all 0.3s ease-in-out;
}
.error a:hover, .error a:focus {
  border-bottom-color: #f74440;
}

@media only screen and (min-width: 768px) {
  .error {
    font-size: 24px;
  }
}