$brand-colour: #0078c9;
$brand-dark-colour: #006AB0;
$brand-light-colour: #0081D6;
$tablet-width: 768px;
$desktop-width: 1200px;@import "tools/variables";
@import "tools/mixins";

@import "third-party/google-fonts";
@import "third-party/reset";

@import "base";

.error {
    font-size: 16px;
    line-height: 1.2;
    color: #f74440;

    a {
        font-weight: 700;
        color: inherit;
        text-decoration: none;
        border-bottom: transparent solid 2px;
        transition: all .3s ease-in-out;

        &:hover,
        &:focus, {
            border-bottom-color: #f74440;
        }
    }
}

@media only screen and (min-width: $tablet-width) {
    .error {
        font-size: 24px;
    }
}
